import { Screen } from './interfaces/game.interface'
import { Location, Player } from './interfaces/player.interface'
import { SubscribeType } from './interfaces/notify.interface'

export default class Game {
  players: Record<string, Location>
  fruits: any
  screen: Screen

  constructor() {
    this.players = {}
    this.fruits
    this.screen = {
      height: 10,
      width: 10
    }
  }

  private observers: Function[] = []

  subscribe(observerFunction: Function) {
    this.observers.push(observerFunction)
  }

  private notifyAll<K extends keyof SubscribeType>(key: K, payload: SubscribeType[K]) {
    for (const observerFunction of this.observers) {
      observerFunction(key, payload)
    }
  }

  addPlayer(command: Player) {
    const playerId = command.playerId
    const playerX =
      'playerX' in command ? command.playerX : Math.floor(Math.random() * this.screen.width)
    const playerY =
      'playerY' in command ? command.playerY : Math.floor(Math.random() * this.screen.height)

    this.players[playerId] = {
      x: playerX,
      y: playerY
    }
  }
}
